body{
    background-image: url("./mine.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
  
}
h1{
    text-align: center;
    
    background: -webkit-linear-gradient(rgb(255, 238, 0), rgb(21, 0, 255));
    /* -webkit-background-clip: text; */
    -webkit-text-fill-color: transparent;
    font-size: 40px;
}
.cookieBtn{
    
    background-image: url("./bitcoin.jpg");
    height: 200px;
    width: 200px;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-size: cover;
background-position: center;
justify-content: center;
outline : none
}
.cookieContainer{
    display: flex;
      flex-direction: column;
      
}
.nbrCookies{
    display: flex;
    justify-content: center;
}
p{
    font-size: 20px;
    font-weight: bold;
    color: white;
}
.nbrBitcoin{
    font-size: 40px;
}
.divCookBtn{
    display: flex;
    justify-content: center;
}

.shopbtc{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
.nbrCpu{
    display: flex;
    justify-content: center;
}

.CpuBtn{
    background-image: url("./CPU.jpg");
    height: 150px;
    width: 150px;
    border-radius: 20%;
    background-repeat: no-repeat;
    background-size: cover;
background-position: center;
justify-content: center;
outline : none
}

.GraphBtn{
    background-image: url("./graphcard.jpg");
    height: 150px;
    width: 150px;
    border-radius: 20%;
    background-repeat: no-repeat;
    background-size: cover;
background-position: center;
justify-content: center;
outline : none
}

.materielbtc{
    text-align: center;
    background: rgba( 255, 255, 255, 0.10 );
    box-shadow: 0 8px 32px 0 rgba(130, 135, 31, 0.37);
    backdrop-filter: blur( 0.5px );
    -webkit-backdrop-filter: blur( 0.5px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}